'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Progress } from '@/components/ui/progress'

interface ExportResult {
  metadata: {
    exportedAt: string;
    sourceStore: string;
    storeNumber: number;
    version: string;
    totalItems: Record<string, number>;
  };
  data: any;
}

interface ImportResult {
  success: boolean;
  message: string;
  targetStore: string;
  importedAt: string;
  summary: Record<string, number>;
}

export default function SyncPage() {
  // Export state
  const [exportLoading, setExportLoading] = useState(false)
  const [exportResult, setExportResult] = useState<ExportResult | null>(null)
  const [exportError, setExportError] = useState<string | null>(null)
  const [selectedStore, setSelectedStore] = useState<string>('')
  const [customExportConfig, setCustomExportConfig] = useState({
    apiKey: '',
    apiSecret: '',
    accessToken: '',
    shopDomain: ''
  })
  const [useCustomExport, setUseCustomExport] = useState(false)

  // Import state
  const [importLoading, setImportLoading] = useState(false)
  const [importResult, setImportResult] = useState<ImportResult | null>(null)
  const [importError, setImportError] = useState<string | null>(null)
  const [importData, setImportData] = useState<string>('')
  const [importFile, setImportFile] = useState<File | null>(null)
  const [targetStore, setTargetStore] = useState<string>('')
  const [customImportConfig, setCustomImportConfig] = useState({
    apiKey: '',
    apiSecret: '',
    accessToken: '',
    shopDomain: ''
  })
  const [useCustomImport, setUseCustomImport] = useState(false)
  const [importProgress, setImportProgress] = useState(0)

  const handleExport = async () => {
    setExportLoading(true)
    setExportError(null)
    setExportResult(null)

    try {
      let url = '/api/export-store'
      let options: RequestInit = { method: 'GET' }

      if (useCustomExport) {
        // Use custom configuration
        options = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(customExportConfig)
        }
      } else {
        // Use predefined store
        if (!selectedStore) {
          throw new Error('Please select a store to export')
        }
        url += `?store=${selectedStore}`
      }

      const response = await fetch(url, options)
      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Export failed')
      }

      setExportResult(data)
    } catch (error) {
      setExportError(error instanceof Error ? error.message : 'Export failed')
    } finally {
      setExportLoading(false)
    }
  }

  const handleDownloadExport = () => {
    if (!exportResult) return

    const dataStr = JSON.stringify(exportResult, null, 2)
    const dataBlob = new Blob([dataStr], { type: 'application/json' })
    const url = URL.createObjectURL(dataBlob)
    const link = document.createElement('a')
    link.href = url
    link.download = `store-export-${Date.now()}.json`
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
    URL.revokeObjectURL(url)
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    setImportFile(file)
    const reader = new FileReader()
    reader.onload = (e) => {
      const content = e.target?.result as string
      setImportData(content)
    }
    reader.readAsText(file)
  }

  const handleImport = async () => {
    setImportLoading(true)
    setImportError(null)
    setImportResult(null)
    setImportProgress(0)

    try {
      if (!importData) {
        throw new Error('Please provide import data')
      }

      let parsedData
      try {
        parsedData = JSON.parse(importData)
      } catch {
        throw new Error('Invalid JSON format')
      }

      const requestBody: any = {
        data: parsedData
      }

      if (useCustomImport) {
        requestBody.customConfig = customImportConfig
      } else {
        if (!targetStore) {
          throw new Error('Please select a target store')
        }
        requestBody.storeNumber = parseInt(targetStore)
      }

      // Simulate progress updates
      const progressInterval = setInterval(() => {
        setImportProgress(prev => Math.min(prev + 10, 90))
      }, 500)

      const response = await fetch('/api/import-store', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(requestBody)
      })

      clearInterval(progressInterval)
      setImportProgress(100)

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Import failed')
      }

      setImportResult(data)
    } catch (error) {
      setImportError(error instanceof Error ? error.message : 'Import failed')
      setImportProgress(0)
    } finally {
      setImportLoading(false)
    }
  }

  return (
    <div className="px-4 py-8">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Store Sync Dashboard
          </h1>
          <p className="text-gray-600">
            Export and import store configurations between Shopify stores
          </p>
        </div>

        <Tabs defaultValue="export" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="export">Export Store</TabsTrigger>
            <TabsTrigger value="import">Import Store</TabsTrigger>
          </TabsList>

          <TabsContent value="export" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Export Store Configuration</CardTitle>
                <CardDescription>
                  Export your store's themes, products, collections, and settings to a JSON file
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id="useCustomExport"
                    checked={useCustomExport}
                    onChange={(e) => setUseCustomExport(e.target.checked)}
                    className="rounded"
                  />
                  <Label htmlFor="useCustomExport">Use custom store configuration</Label>
                </div>

                {useCustomExport ? (
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="exportApiKey">API Key</Label>
                      <Input
                        id="exportApiKey"
                        value={customExportConfig.apiKey}
                        onChange={(e) => setCustomExportConfig(prev => ({ ...prev, apiKey: e.target.value }))}
                        placeholder="Your Shopify API Key"
                      />
                    </div>
                    <div>
                      <Label htmlFor="exportApiSecret">API Secret</Label>
                      <Input
                        id="exportApiSecret"
                        type="password"
                        value={customExportConfig.apiSecret}
                        onChange={(e) => setCustomExportConfig(prev => ({ ...prev, apiSecret: e.target.value }))}
                        placeholder="Your Shopify API Secret"
                      />
                    </div>
                    <div>
                      <Label htmlFor="exportAccessToken">Access Token</Label>
                      <Input
                        id="exportAccessToken"
                        type="password"
                        value={customExportConfig.accessToken}
                        onChange={(e) => setCustomExportConfig(prev => ({ ...prev, accessToken: e.target.value }))}
                        placeholder="Store Access Token"
                      />
                    </div>
                    <div>
                      <Label htmlFor="exportShopDomain">Shop Domain</Label>
                      <Input
                        id="exportShopDomain"
                        value={customExportConfig.shopDomain}
                        onChange={(e) => setCustomExportConfig(prev => ({ ...prev, shopDomain: e.target.value }))}
                        placeholder="your-store-name"
                      />
                    </div>
                  </div>
                ) : (
                  <div>
                    <Label htmlFor="storeSelect">Select Store</Label>
                    <Select value={selectedStore} onValueChange={setSelectedStore}>
                      <SelectTrigger>
                        <SelectValue placeholder="Choose a store to export" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="1">Store 1</SelectItem>
                        <SelectItem value="2">Store 2</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                )}

                <Button 
                  onClick={handleExport} 
                  disabled={exportLoading}
                  className="w-full"
                >
                  {exportLoading ? 'Exporting...' : 'Export Store Configuration'}
                </Button>

                {exportError && (
                  <Alert variant="destructive">
                    <AlertDescription>{exportError}</AlertDescription>
                  </Alert>
                )}

                {exportResult && (
                  <Card className="bg-green-50 border-green-200">
                    <CardHeader>
                      <CardTitle className="text-green-800">Export Successful</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-2 text-sm">
                        <p><strong>Source Store:</strong> {exportResult.metadata.sourceStore}</p>
                        <p><strong>Exported At:</strong> {new Date(exportResult.metadata.exportedAt).toLocaleString()}</p>
                        <p><strong>Total Items:</strong></p>
                        <ul className="ml-4 space-y-1">
                          {Object.entries(exportResult.metadata.totalItems).map(([key, value]) => (
                            <li key={key}>• {key}: {value}</li>
                          ))}
                        </ul>
                      </div>
                      <Button onClick={handleDownloadExport} className="mt-4">
                        Download JSON File
                      </Button>
                    </CardContent>
                  </Card>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="import" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Import Store Configuration</CardTitle>
                <CardDescription>
                  Import a previously exported store configuration to replicate setup
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label htmlFor="importFile">Upload JSON File</Label>
                  <Input
                    id="importFile"
                    type="file"
                    accept=".json"
                    onChange={handleFileUpload}
                    className="mt-1"
                  />
                </div>

                <div>
                  <Label htmlFor="importData">Or Paste JSON Data</Label>
                  <Textarea
                    id="importData"
                    value={importData}
                    onChange={(e) => setImportData(e.target.value)}
                    placeholder="Paste your exported JSON configuration here..."
                    rows={6}
                    className="mt-1"
                  />
                </div>

                <div className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id="useCustomImport"
                    checked={useCustomImport}
                    onChange={(e) => setUseCustomImport(e.target.checked)}
                    className="rounded"
                  />
                  <Label htmlFor="useCustomImport">Use custom target store configuration</Label>
                </div>

                {useCustomImport ? (
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="importApiKey">API Key</Label>
                      <Input
                        id="importApiKey"
                        value={customImportConfig.apiKey}
                        onChange={(e) => setCustomImportConfig(prev => ({ ...prev, apiKey: e.target.value }))}
                        placeholder="Target Store API Key"
                      />
                    </div>
                    <div>
                      <Label htmlFor="importApiSecret">API Secret</Label>
                      <Input
                        id="importApiSecret"
                        type="password"
                        value={customImportConfig.apiSecret}
                        onChange={(e) => setCustomImportConfig(prev => ({ ...prev, apiSecret: e.target.value }))}
                        placeholder="Target Store API Secret"
                      />
                    </div>
                    <div>
                      <Label htmlFor="importAccessToken">Access Token</Label>
                      <Input
                        id="importAccessToken"
                        type="password"
                        value={customImportConfig.accessToken}
                        onChange={(e) => setCustomImportConfig(prev => ({ ...prev, accessToken: e.target.value }))}
                        placeholder="Target Store Access Token"
                      />
                    </div>
                    <div>
                      <Label htmlFor="importShopDomain">Shop Domain</Label>
                      <Input
                        id="importShopDomain"
                        value={customImportConfig.shopDomain}
                        onChange={(e) => setCustomImportConfig(prev => ({ ...prev, shopDomain: e.target.value }))}
                        placeholder="target-store-name"
                      />
                    </div>
                  </div>
                ) : (
                  <div>
                    <Label htmlFor="targetStoreSelect">Select Target Store</Label>
                    <Select value={targetStore} onValueChange={setTargetStore}>
                      <SelectTrigger>
                        <SelectValue placeholder="Choose target store for import" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="1">Store 1</SelectItem>
                        <SelectItem value="2">Store 2</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                )}

                {importLoading && (
                  <div className="space-y-2">
                    <Label>Import Progress</Label>
                    <Progress value={importProgress} className="w-full" />
                    <p className="text-sm text-gray-600">{importProgress}% complete</p>
                  </div>
                )}

                <Button 
                  onClick={handleImport} 
                  disabled={importLoading || !importData}
                  className="w-full"
                >
                  {importLoading ? 'Importing...' : 'Import Store Configuration'}
                </Button>

                {importError && (
                  <Alert variant="destructive">
                    <AlertDescription>{importError}</AlertDescription>
                  </Alert>
                )}

                {importResult && (
                  <Card className="bg-green-50 border-green-200">
                    <CardHeader>
                      <CardTitle className="text-green-800">Import Successful</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-2 text-sm">
                        <p><strong>Target Store:</strong> {importResult.targetStore}</p>
                        <p><strong>Imported At:</strong> {new Date(importResult.importedAt).toLocaleString()}</p>
                        <p><strong>Items Imported:</strong></p>
                        <ul className="ml-4 space-y-1">
                          {Object.entries(importResult.summary).map(([key, value]) => (
                            <li key={key}>• {key}: {value}</li>
                          ))}
                        </ul>
                      </div>
                    </CardContent>
                  </Card>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
